<script>
import {getWallet} from '@API/user'
export default {
    name: "userWallet",
    data() {
        return {
            userObj: {}
        }
    },
    methods: {
        goPage: function(name, value) {
            this.$router.push({name: name, query: value});
        },
        $getWallet() {
            getWallet().then(res => {
                if(res.code == 1) {
                    this.userObj = res.data;
                }
            })
        }
    },
    mounted() {
        this.$getWallet();
    }
}
